body{--color-text: #fff;--color-bg: #ececec;--color-link: #fff;--color-link-hover: #fff;--color-info: #fff;font-family:'Buenard', serif;color:#fff;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.committee__header-name{font-size:3rem;margin-top:1rem;padding:0 0 2rem 3.5rem;text-transform:uppercase;font-weight:bold;text-align:left}@media screen and (max-width: 720px){.committee__header-name{font-size:1.2rem;margin-bottom:2.3rem;padding:0 0 1rem 2rem}}@media screen and (max-width: 720px){.committee-wrapper{margin-top:5rem}}.preview-open{overflow:hidden}@-webkit-keyframes loaderAnim{to{opacity:1;-webkit-transform:scale3d(0.5, 0.5, 1);transform:scale3d(0.5, 0.5, 1)}}@keyframes loaderAnim{to{opacity:1;-webkit-transform:scale3d(0.5, 0.5, 1);transform:scale3d(0.5, 0.5, 1)}}a{text-decoration:none;color:var(--color-link);outline:none}a:hover,a:focus{color:var(--color-link-hover);outline:none}.hidden{position:absolute;overflow:hidden;width:0;height:0;pointer-events:none}.icon{display:block;width:1.5em;height:1.5em;margin:0 auto;fill:currentColor}main{position:relative}.content{position:relative}.codrops-header{position:relative;z-index:100;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:flex-start;align-items:flex-start;-webkit-align-items:center;align-items:center;padding:2rem}.codrops-header__title{font-size:1em;font-weight:bold;margin:0;padding:0}.info{margin:0 0 0 1.25em;color:var(--color-info)}.github{display:block;margin:0 0 0 auto}.codrops-links{position:relative;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;margin:0 1em 0 0;text-align:center;white-space:nowrap}.codrops-icon{display:inline-block;margin:0.15em;padding:0.25em}.grid{width:100%;max-width:1440px;margin:0 auto;padding-bottom:10rem}.grid__item{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;margin:0 0 10rem 0;cursor:pointer}.box{position:relative;margin:2rem}.box__title{margin:0;line-height:1;position:absolute;z-index:100}.box__title-inner{display:block;position:relative;font-weight:normal;text-transform:uppercase;font-size:4.15rem;letter-spacing:0.15rem;line-height:1.25;font-family:'Anton', sans-serif}.box__title-inner[data-hover]{-webkit-text-stroke:2px #fff;-webkit-text-fill-color:transparent;color:transparent}.box__title-inner::before{content:attr(data-hover);position:absolute;top:0;left:0;height:0;overflow:hidden;white-space:nowrap;-webkit-text-stroke:0;-webkit-text-fill-color:#fff;color:#fff;transition:all 0.3s}.grid__item:hover .box__title-inner::before{height:100%;width:100%}.box__text{margin:0;position:absolute;top:-6rem;font-weight:normal;text-transform:uppercase;letter-spacing:0.15rem;font-size:1.75rem;margin:0.5rem 1rem;font-family:'Anton', sans-serif}.box__text-inner{position:relative;display:block;border:6px solid var(--color-text);padding:0.25rem 1.25rem}.box__text-inner--reverse{background:var(--color-text);color:var(--color-bg)}.box__img{display:block;-webkit-flex:none;flex:none;margin:0 auto;max-width:100%;-webkit-filter:grayscale(1);filter:grayscale(1);transition:-webkit-filter 0.3s;transition:filter 0.3s;transition:filter 0.3s, -webkit-filter 0.3s;pointer-events:none}.grid__item:hover .box__img:not(.box__img--original){-webkit-filter:grayscale(0);filter:grayscale(0)}.box__deco{font-size:6rem;line-height:1;font-weight:bold;position:absolute;bottom:-4rem;right:-4rem;display:none}.box__content{position:absolute;max-width:195px;font-size:0.9rem;text-align:right;display:none}.quote__top{top:-50px}.box__content strong{white-space:nowrap}@media screen and (min-width: 55em){.grid{display:grid;-webkit-align-items:center;align-items:center;padding:3rem 3rem 15rem 3rem;grid-row-gap:5rem;grid-template-columns:repeat(3, calc(100% / 3))}.grid__item{margin:0}.box{margin:4rem}.box__title{top:-4rem;right:-4.5rem;-webkit-writing-mode:vertical-rl;-ms-writing-mode:tb-rl;writing-mode:vertical-rl}.box__title--straight{-webkit-writing-mode:horizontal-tb;-ms-writing-mode:lr-tb;writing-mode:horizontal-tb}.box__title--straight .box__title-inner::before{width:0;height:auto}.box__title--bottom{bottom:-5rem;top:auto}.box__title--left{left:-2rem;right:auto}.box__text--topcloser{top:-5rem}.box__text--bottom{bottom:-5rem;top:auto}.box__text--bottomcloser{bottom:-5rem;top:auto}.box__text--right{right:0;left:auto}.box__text-inner--rotated1{-webkit-transform:rotate(4deg);transform:rotate(4deg)}.box__text-inner--rotated2{-webkit-transform:rotate(-3deg);transform:rotate(-3deg)}.box__text-inner--rotated3{-webkit-transform:rotate(-15deg);transform:rotate(-15deg)}.box__deco{display:block}.box__deco--left{right:auto;left:-3rem}.box__deco--top{top:0;bottom:auto}}@media screen and (min-width: 80em){.grid{grid-template-columns:repeat(2, 50%)}.grid__item:nth-child(2n-2){margin-top:-6rem;margin-bottom:6rem}.grid__item:nth-child(2n){margin-top:-5rem}.box{margin:6rem 4.5rem}.box__content{display:block}}@media screen and (max-width: 55em){.content{-webkit-flex-direction:column;flex-direction:column;height:auto;min-height:0}.content--fixed{position:relative;z-index:1000;display:block;padding:0.85em}}

